* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  max-width: 900px;
  width: 100%;
  text-align: center;
  margin: 0 auto;
  font-family: Cambria, Cochin, Georgia, Times, Times New Roman, serif;
  position: relative;
}

.timer {
  justify-content: center;
  gap: 50px;
  margin-top: 30px;
  display: flex;
}

.date-form {
  width: 150px;
  text-transform: uppercase;
  border: 2px solid #2f4f4f;
  border-radius: 3px;
  padding: 5px;
  font-family: inherit;
  font-size: 15px;
}

.start-btn {
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #2f4f4f;
  border-radius: 3px;
  padding: 5px;
  font-family: inherit;
  font-size: 15px;
}

.start-btn:disabled {
  background-color: #d3d3d3;
}

.value {
  font-size: 30px;
}

.label {
  font-size: 15px;
  display: block;
}

.tooltip {
  color: red;
  border-radius: 3px;
  font-size: 15px;
  display: inline-block;
  position: absolute;
  top: -25%;
  right: 50%;
  transform: translate(50%);
}

.hidden {
  opacity: 0;
}

